.dashboard-container {
  max-width: 600px;
  margin: 80px auto;
  padding: 40px;
  background: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 0 15px rgba(0,0,0,0.1);
  text-align: center;
  font-family: 'Segoe UI', sans-serif;
}

.dashboard-container h1 {
  font-size: 28px;
  margin-bottom: 15px;
}

.dashboard-container p {
  font-size: 16px;
  margin-bottom: 25px;
}

.dashboard-container button {
  padding: 10px 20px;
  font-size: 15px;
  background-color: #222;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
